import React from 'react'
import {withApollo, gql} from 'react-apollo'
import Item from './Item'
import { Button } from 'antd';

class ItemList extends React.Component{

    constructor(props){
    super()
    this.state = {
        items: [],
        cargando:true,
        existe:true
    }
        this._executeSearch=this._executeSearch.bind(this)
    }

    componentWillMount(){
      var searchText=this.props.match.params.searchText
        this._executeSearch(searchText)
    }
    componentWillReceiveProps(nextProps) {
      var searchText=nextProps.match.params.searchText
       this._executeSearch(searchText)

    }
    render(){
        return(
            <div>
      {this.state.items.map((item, index) => (
        <Item key={item.id} item={item} index={index}/>
      ))
    }{this.state.cargando?<h1>Cargando..</h1>:null}
    {this.state.existe?null:<h1>No se encontraron resultados</h1>}
           </div>
           )
    }

    _executeSearch = async (searchText) => {
      this.setState({cargando:true})

    if(searchText==='todos'){
      var result = await this.props.client.query({
      query: ALL_ITEMS_QUERY
      })
    }else{
      var result = await this.props.client.query({
      query: ALL_ITEMS_SEARCH_QUERY,
      variables: { searchText }
      })
    }

    const items = result.data.allItems
    this.setState({items})
    this.setState({cargando:false})

    if(this.state.items.length>0){
      this.setState({existe:true})
    }else{
      this.setState({existe:false})
    }


  }

}


const ALL_ITEMS_SEARCH_QUERY = gql`
  query AllItemsSearchQuery($searchText: String!) {
    allItems(filter: {
            type_contains: $searchText
          }) {
      id
      name
      description

      type
    }
  }
`

const ALL_ITEMS_QUERY=gql`
query AllItemsQuery{
    allItems{
        id
        name
    }
}

`
export default withApollo(ItemList);



