import React from 'react'
import { Menu, Icon } from 'antd';
import {Link} from 'react-router-dom'
const SubMenu = Menu.SubMenu;
const MenuItemGroup = Menu.ItemGroup;


class MiHeader extends React.Component {
  state = {
    current: 'mail',
  }
  handleClick = (e) => {
    console.log('click ', e);
    this.setState({
      current: e.key,
    });
  }

  render() {
    return (
      <Menu
        onClick={this.handleClick}
        selectedKeys={[this.state.current]}
        mode="horizontal"
      >
        <Menu.Item key="mail">
        <Link to='/'>
          <Icon type="mail" />Bienvenidos
          </Link>
        </Menu.Item>
        <Menu.Item key="app" >
          <Link to='/contacto'>
          <Icon type="appstore" />Contacto
          </Link>
        </Menu.Item>
        <SubMenu title={<span><Icon type="setting" />Mis Trabajos</span>}>
          <MenuItemGroup >

            <Menu.Item key="setting:1">
            <Link to='/portafolio/pinturas'>Pinturas</Link>
            </Menu.Item>
            <Menu.Item key="setting:2"><Link to='/portafolio/dibujos'>Dibujos</Link></Menu.Item>
            <Menu.Item key="setting:3"><Link to='/portafolio/textiles'>Textiles</Link></Menu.Item>
            <Menu.Item key="setting:4"><Link to='/portafolio/exposiciones'>Exposiciones</Link></Menu.Item>
          </MenuItemGroup>
        </SubMenu>
      </Menu>
    );
  }
}

export default MiHeader;



